import React, { useRef, useEffect, useState } from 'react'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Fade from 'react-reveal/Fade'
import { Container, HighlightBtn, TransBtn, MenuToggleBtn } from './styles'
import { Flex, Box } from '../../style/globalStyles/GlobalStyles'
import LogoImg from '../../assets/images/logo.svg'
import MenuToggleImg from '../../assets/images/menu-toggle.svg'

export const Header = () => {
  const [currentWidth, setWidth] = useState(
    typeof window !== 'undefined' && window.innerWidth
  )
  const updateWidthAndHeight = () => {
    setWidth(typeof window !== 'undefined' && window.innerWidth)
  }
  useEffect(() => {
    typeof window !== 'undefined' &&
      window.addEventListener('resize', updateWidthAndHeight)
  }, [])

  return (
    <Container>
      <Fade top duration={1000}>
        <Row>
          <Col xs lg="2" md="4">
            <Flex className="logo">
              <img src={LogoImg} alt="Brand" />
            </Flex>
          </Col>
          <Col xs lg="10" md="8">
            <Flex justifyContent="flex-end" pt="2px">
              {currentWidth < 768 && (
                <Box>
                  <MenuToggleBtn>
                    <img src={MenuToggleImg} alt="Toggle" />
                  </MenuToggleBtn>
                </Box>
              )}
              {currentWidth > 767 && (
                <>
                  <Box mr={2}>
                    <HighlightBtn className="hvr-grow">Pay</HighlightBtn>
                  </Box>
                  <Box ml={2}>
                    <TransBtn
                      className="hvr-grow"
                      styles={{ margin: '20px !important' }}
                    >
                      Employers
                    </TransBtn>
                  </Box>
                </>
              )}
            </Flex>
          </Col>
        </Row>
      </Fade>
    </Container>
  )
}

export default Header
